import { ModalRegistradoPf } from './modal-registropf/modal-registropf.components';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from '../../environments/environment';
import { Alert } from 'selenium-webdriver';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { RegistroPfService } from './services/registropf.service';

@Component({
  selector: 'app-registroPf',
  templateUrl: './registropf.component.html',
  styleUrls: ['./registropf.component.css']
})
export class RegistroPfComponent implements OnInit {
  
  lista: [] = [];
  page = 0;
  pageSize = 50;
  collectionSize =  0;
  total = 0;
  carregando = false;
  queryBusca = "";
  url_base = `${environment.API}`;
  alerts: any;
  subscription = null;
  
  constructor(
    private router: Router,
    public service: RegistroPfService,
    private modalService: NgbModal
    ) {    }

  ngOnInit() {
   
  }

  novo(){
    this.router.navigate(['/registroPf',  'novo']); 
   }

   openModal(registro) {

        this.subscription = this.service.getRegistroPessoaFisica(registro.codigo)
        .subscribe(
          response => {
            console.log("Deu certo: " + response.dados);
            var currentObject = response.dados[0];
            const modalRef = this.modalService.open(ModalRegistradoPf,  { size: 'lg' });
            console.log('Antes');
            modalRef.componentInstance.registro = response.dados[0];            
//            modalRef.componentInstance.loadDados(registro);
          });        

    console.log('Depois');   

  }   

   pesquisar() {
    this.alerts = [];
    this.carregando = true;
    this.subscription = this.service.getListaRegistroPessoaFisica(this.queryBusca)
      .subscribe(
        response => {
          console.log("Status" + response.status);          
          if (response.status.codigo == 0){
            this.lista = response.dados;
          }else{
            this.lista = [];
            this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
          }
        this.carregando = false;
        },
        err => {
          this.carregando = false;
          this.alerts = Array.from([{ type: 'danger', message: err.message }])          
          console.log(err);
        });

      this.collectionSize =  this.lista.length;
   
  }

  close(alert: Alert) {
    this.alerts.splice(this.alerts.indexOf(alert), 1);
  }

  ngOnDestroy() {
    if  (this.subscription != null){
      this.subscription.unsubscribe();
    } 
  }
}