import { ModalRegistradoPf } from './modal-registropf/modal-registropf.components';
import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { RegistroPfComponent } from './registropf.component';
import { RegistroPfGuard } from '../guards/registroPf.guard';


const registropfRoutes = [
    {path: '', component: RegistroPfComponent, 
     canActivateChild: [RegistroPfGuard],
     children : [
        {path: 'novo', component: ModalRegistradoPf}
    ]}
];
@NgModule({
    imports: [RouterModule.forChild(registropfRoutes)],
    exports: [RouterModule]
})
export class RegistroPfRoutingModule {}
