import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { tap, delay, take } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class RegistroPfService {

  private readonly API = `${environment.API}entidade`;
  private readonly API_GET_LISTA_REGISTROS_PESSOAS_FISICA = `${environment.API}registro/pessoaFisica/getLista`;  
  private readonly API_GET_REGISTRO_PESSOAS_FISICA = `${environment.API}registro/pessoaFisica/get`;  
  private readonly API_GERAR_CARTEIRA_PESSOA_FISICA = `${environment.API}pessoaFisica/carteira`;    
  private readonly API_SALVAR_REGISTRO_PESSOA_FISICA = `${environment.API}registro/pessoaFisica/salvar`;    
  private readonly API_LOAD_COMBOS = `${environment.API}tabelas/loadCombos`;    
  
  private httpOptions = {
    headers: new HttpHeaders({
      'X-Requested-With': 'XMLHttpRequest',
      'Cache-Control':	'no-cache',
      'Content-Type': 'application/json; charset=utf-8'
    })
  };

    
  constructor(private http: HttpClient) { }

  
  getListaRegistroPessoaFisica( search) {
    return this.http.post(this.API_GET_LISTA_REGISTROS_PESSOAS_FISICA,{campos:[{ campo: 'entidade.nome', valor: search, operador: 'LIKE', condicao: ' OR ' }, { campo: 'pessoafisica.cpf', valor: search, operador: 'LIKE', condicao: ' OR '}]}, this.httpOptions)
    .pipe(
       tap(console.log)
    );

      
  }

  getRegistroPessoaFisica( search) {
    return this.http.post(this.API_GET_REGISTRO_PESSOAS_FISICA,{campos:[{ campo: 'registro.entidade', valor: search, operador: '=', condicao: '' }]}, this.httpOptions)
    .pipe(
       tap(console.log)
    );
      
  }


  gerarCarteiraPessoaFisica( codigoEntidade) {
    return this.http.get(this.API_GERAR_CARTEIRA_PESSOA_FISICA + "/" + codigoEntidade, { responseType: "blob"  } )
      .pipe(
        tap(console.log)
      );
  }


  loadCombos() {
    return this.http.post(this.API_LOAD_COMBOS,{}, this.httpOptions)
      .pipe(
        tap(console.log)
      );
  }



  salvar(form) {
    let formObj = form.getRawValue(); // {name: '', description: ''}

    let serializedForm = JSON.stringify(formObj);

    return this.http.post(this.API_SALVAR_REGISTRO_PESSOA_FISICA, serializedForm, this.httpOptions)
    .pipe(
       tap(console.log)
    );
  }

    
}